.Footer {
    position: sticky;
    width: 100%;
    height: 6rem;
    bottom: 0rem;
    display: flex;
    flex-wrap: wrap;
    margin-top: 2rem;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    box-sizing: border-box;
}

@media screen and (max-width:1000px) {
    .Footer {
        justify-content: center;
    }
}